'use client';

import { Booking } from "@/src/lib/booking/BookingTypes";
import { SubscriptionPackage } from "@/src/lib/subscriptionPackages/subscriptionPackagesType";
import { Button } from '@/src/components/ui/button';
import { BadgeCheck, Bike, Building2, CalendarDays, DollarSign, Phone, User, Clock } from 'lucide-react';
import { toast } from 'sonner';
import { format, differenceInDays } from 'date-fns';

interface Props {
  bookings: Booking[];
  subscriptionPackages?: SubscriptionPackage[];
  onEdit: (booking: Booking) => void;
  onDelete: (id: string) => void;
}

export default function BookingTable({
  bookings,
  subscriptionPackages = [],
  onEdit,
  onDelete,
}: Props) {
  const sorted = [...bookings].sort((a, b) => {
    const aTime = a.createdAt instanceof Date ? a.createdAt.getTime() : 0;
    const bTime = b.createdAt instanceof Date ? b.createdAt.getTime() : 0;
    return bTime - aTime;
  });

  const getPackageName = (id: string) => {
    return subscriptionPackages.find(pkg => pkg.id === id)?.name || 'N/A';
  };

  const formatDate = (date: any) => {
    return typeof date?.toDate === 'function'
      ? format(date.toDate(), 'dd/MM/yyyy')
      : 'N/A';
  };

  const formatDateTime = (date: any) => {
    return date instanceof Date
      ? format(date, 'dd/MM/yyyy HH:mm')
      : typeof date?.toDate === 'function'
        ? format(date.toDate(), 'dd/MM/yyyy HH:mm')
        : 'N/A';
  };

  const getRentalDuration = (start: any, end: any) => {
    if (typeof start?.toDate === 'function' && typeof end?.toDate === 'function') {
      const startDate = start.toDate();
      const endDate = end.toDate();
      const days = differenceInDays(endDate, startDate) + 1;
      return `${format(startDate, 'dd/MM/yyyy')} ‚Üí ${format(endDate, 'dd/MM/yyyy')} (${days} days)`;
    }
    return 'N/A';
  };

  const handleEdit = (booking: Booking) => {
    toast.info(`‚úèÔ∏è Editing booking for ${booking.customerName}`);
    onEdit(booking);
  };

  const handleDelete = (id: string) => {
    toast.success('üóëÔ∏è Booking deleted');
    onDelete(id);
  };

  return (
    <div className="overflow-x-auto bg-white shadow rounded-xl p-4">
      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100">
          <tr>
            <th className="px-3 py-2 border">Customer</th>
            <th className="px-3 py-2 border">Vehicle Info</th>
            <th className="px-3 py-2 border">Pricing</th>
            <th className="px-3 py-2 border">Rental Period</th>
            <th className="px-3 py-2 border">Station</th>
            <th className="px-3 py-2 border">Booked At</th>
            <th className="px-3 py-2 border">Actions</th>
          </tr>
        </thead>
        <tbody>
          {sorted.length === 0 ? (
            <tr>
              <td colSpan={7} className="text-center py-4 text-gray-500">No bookings found.</td>
            </tr>
          ) : (
            sorted.map((b) => (
              <tr key={b.id} className="border-t hover:bg-gray-50">
                <td className="px-3 py-2 border align-top">
                  <div className="font-medium flex items-center gap-1"><User className="w-4 h-4" /> {b.customerName}</div>
                  <div className="text-xs text-gray-500 flex items-center gap-1"><Phone className="w-3 h-3" /> {b.phone}</div>
                  <div className="text-xs text-gray-500">ID: {b.idNumber}</div>
                </td>
                <td className="px-3 py-2 border align-top">
                  <div className="font-medium flex items-center gap-1"><Bike className="w-4 h-4" /> {b.vehicleModel} ({b.vehicleColor})</div>
                  <div className="text-xs text-gray-500">VIN: {b.vin}</div>
                  <div className="text-xs text-gray-500">Plate: {b.licensePlate}</div>
                  <div className="text-xs text-gray-400">üîã {b.batteryCodes?.join(', ')}</div>
                </td>
                <td className="px-3 py-2 border align-top">
                  <div className="flex items-center gap-1 text-sm font-semibold text-green-600">
                    <DollarSign className="w-4 h-4" /> {b.total.toLocaleString()}‚Ç´
                  </div>
                  <div className="text-xs text-gray-500">Package: {getPackageName(b.subscriptionPackageId)}</div>
                  <div className="text-xs text-gray-500">Deposit: {b.deposit.toLocaleString()}‚Ç´</div>
                  <div className="text-xs text-gray-500">Remaining: {b.remaining.toLocaleString()}‚Ç´</div>
                </td>
                <td className="px-3 py-2 border align-top text-xs text-gray-600">
                  <div className="flex items-start gap-1">
                    <Clock className="w-4 h-4 text-gray-500 mt-[2px]" />
                    <span>{getRentalDuration(b.startDate, b.endDate)}</span>
                  </div>
                </td>
                <td className="px-3 py-2 border align-top">
                  <div className="flex items-center gap-1 text-sm"><Building2 className="w-4 h-4" /> {b.stationId}</div>
                  <div className="text-xs text-gray-500">Company: {b.companyId}</div>
                </td>
                <td className="px-3 py-2 border align-top text-xs text-gray-600">
                  <div className="flex items-center gap-1">
                    <CalendarDays className="w-4 h-4 text-gray-400" />
                    {formatDateTime(b.createdAt)}
                  </div>
                </td>
                <td className="px-3 py-2 border whitespace-nowrap align-top">
                  <div className="flex gap-2">
                    <Button variant="default" onClick={() => handleEdit(b)}>Edit</Button>
                    <Button variant="danger" onClick={() => handleDelete(b.id!)}>Delete</Button>
                  </div>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}
