// lib/subscriptionPackages/getAllSubscriptionPackages.ts

import { db } from "@/src/firebaseConfig";
import { collection, getDocs } from "firebase/firestore";
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';

export async function getAllSubscriptionPackages(): Promise<SubscriptionPackage[]> {
  const querySnapshot = await getDocs(collection(db, "subscriptionPackages"));

  return querySnapshot.docs.map((doc) => {
    const data = doc.data();
    return {
      id: doc.id,
      companyId: data.companyId,
      name: data.name,
      durationType: data.durationType,
      kmLimit: data.kmLimit ?? null,
      chargingMethod: data.chargingMethod,
      basePrice: data.basePrice,
      overageRate: data.overageRate ?? null,
      note: data.note ?? '',
      createdAt: data.createdAt,
      updatedAt: data.updatedAt,
    } as SubscriptionPackage;
  });
}
