'use client';

import { Booking } from "@/src/lib/booking/BookingTypes";
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { useState } from 'react';

interface Props {
  bookings: Booking[];
  setFiltered: (filtered: Booking[]) => void;
  onImport: () => void;
  onExport: () => void;
}

export default function BookingSearchImportExport({
  bookings,
  setFiltered,
  onImport,
  onExport,
}: Props) {
  const [search, setSearch] = useState('');

  const handleSearch = (term: string) => {
    setSearch(term);
    const lower = term.toLowerCase();

    const filtered = bookings.filter((b) =>
      b.customerName?.toLowerCase().includes(lower) ||
      b.phone?.toLowerCase().includes(lower) ||
      b.vehicleModel?.toLowerCase().includes(lower) ||
      b.licensePlate?.toLowerCase().includes(lower) ||
      b.companyId?.toLowerCase().includes(lower)
    );

    setFiltered(filtered);
  };

  return (
    <div className="flex flex-col md:flex-row justify-between items-center gap-4 mb-4">
      <Input
        placeholder="üîç Search bookings..."
        value={search}
        onChange={(e) => handleSearch(e.target.value)}
        className="max-w-sm w-full"
      />

      <div className="flex gap-2">
        <Button variant="outline" onClick={onImport}>
          üì• Import
        </Button>
        <Button onClick={onExport}>
          üì§ Export
        </Button>
      </div>
    </div>
  );
}
