'use client';

import { useEffect, useState } from 'react';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import UserTopMenu from '@/src/components/landingpage/UserTopMenu';
import { useBookingData } from '@/src/components/booking/useBookingData';
import { Booking } from '@/src/lib/booking/BookingTypes';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { getAllSubscriptionPackages } from '@/src/lib/subscriptionPackages/getAllSubscriptionPackages';
import BookingForm from '@/src/components/booking/BookingForm';
import BookingTable from '@/src/components/booking/BookingTable';
import BookingSearchImportExport from '@/src/components/booking/BookingSearchImportExport';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

export default function BookingManagementPage() {
  const { bookings, createBooking, updateBooking, deleteBooking } = useBookingData();
  const [filtered, setFiltered] = useState<Booking[]>(bookings);
  const [editing, setEditing] = useState<Booking | null>(null);
  const [subscriptionPackages, setSubscriptionPackages] = useState<SubscriptionPackage[]>([]);

  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'success' | 'error' | 'info' | 'confirm',
    title: '',
    description: '',
    onConfirm: undefined as (() => void) | undefined,
  });

  const showDialog = (
    type: 'success' | 'error' | 'info',
    title: string,
    description = ''
  ) => {
    setDialog({ open: true, type, title, description, onConfirm: undefined });
  };

  const confirmDelete = (id: string) => {
    const booking = bookings.find((b) => b.id === id);
    if (!booking) return;

    setDialog({
      open: true,
      type: 'confirm',
      title: `Delete booking for ${booking.customerName}?`,
      description: 'This action cannot be undone.',
      onConfirm: async () => {
        try {
          await deleteBooking(id);
          setDialog((prev) => ({ ...prev, open: false }));
          showDialog('success', 'Booking deleted successfully');
        } catch (error) {
          showDialog('error', 'Failed to delete booking');
        }
      },
    });
  };

  const handleSave = async (data: Omit<Booking, 'id' | 'createdAt' | 'updatedAt'>) => {
    try {
      if (editing?.id) {
        await updateBooking(editing.id, data);
        showDialog('success', 'Booking updated successfully');
      } else {
        await createBooking(data);
        showDialog('success', 'Booking created successfully');
      }
      setEditing(null);
    } catch (err) {
      showDialog('error', 'Failed to save booking');
    }
  };

  useEffect(() => {
    getAllSubscriptionPackages().then(setSubscriptionPackages);
  }, []);

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />

      <div className="p-6 mt-1">
        <h1 className="text-2xl font-semibold mb-4 border-b-2 pb-2">Booking Management</h1>

        <BookingSearchImportExport
          bookings={bookings}
          setFiltered={setFiltered}
          onImport={() => showDialog('info', 'üöß Import feature coming soon')}
          onExport={() => showDialog('info', 'üöß Export feature coming soon')}
        />

        <BookingTable
          bookings={filtered.length ? filtered : bookings}
          subscriptionPackages={subscriptionPackages}
          onEdit={(b) => setEditing(b)}
          onDelete={confirmDelete}
        />

        <div className="bg-white shadow rounded-xl p-4 mt-6">
          <BookingForm
            editingBooking={editing}
            onSave={handleSave}
            onCancel={() => setEditing(null)}
          />
        </div>
      </div>

      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
        onConfirm={dialog.onConfirm}
      />
    </div>
  );
}
