'use client';

import { useEffect, useState } from 'react';
import { db } from '@/src/firebaseConfig';
import {
  collection,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  doc,
  Timestamp,
} from 'firebase/firestore';
import { Booking } from "@/src/lib/booking/BookingTypes";

export function useBookingData() {
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [loading, setLoading] = useState(false);

  // üì¶ L·∫•y t·∫•t c·∫£ bookings
  const fetchBookings = async () => {
    setLoading(true);
    try {
      const snapshot = await getDocs(collection(db, 'bookings'));
      const data: Booking[] = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      })) as Booking[];
      setBookings(data);
    } catch (error) {
      console.error("‚ùå Error fetching bookings:", error);
    } finally {
      setLoading(false);
    }
  };

  // ‚ûï Th√™m booking m·ªõi
  const createBooking = async (booking: Omit<Booking, 'id' | 'createdAt' | 'updatedAt'>) => {
    const now = Timestamp.now();
    try {
      const ref = await addDoc(collection(db, 'bookings'), {
        ...booking,
        createdAt: now,
        updatedAt: now,
      });
      await fetchBookings();
      return ref.id;
    } catch (error) {
      console.error("‚ùå Error creating booking:", error);
    }
  };

  // ‚úèÔ∏è C·∫≠p nh·∫≠t booking
  const updateBooking = async (id: string, updated: Partial<Booking>) => {
    try {
      const ref = doc(db, 'bookings', id);
      await updateDoc(ref, {
        ...updated,
        updatedAt: Timestamp.now(),
      });
      await fetchBookings();
    } catch (error) {
      console.error("‚ùå Error updating booking:", error);
    }
  };

  // üóëÔ∏è X√≥a booking
  const deleteBooking = async (id: string) => {
    try {
      await deleteDoc(doc(db, 'bookings', id));
      await fetchBookings();
    } catch (error) {
      console.error("‚ùå Error deleting booking:", error);
    }
  };

  useEffect(() => {
    fetchBookings();
  }, []);

  return {
    bookings,
    loading,
    fetchBookings,
    createBooking,
    updateBooking,
    deleteBooking,
  };
}
